@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* prevent callout to copy image, etc when tap to hold */
  -webkit-touch-callout: none;
  /* prevent copy paste, to allow, change 'none' to 'text' */
  -webkit-user-select: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


header{font-family:"Titillium Web", sans-serif;font-weight:300}header h1{font-size:4rem;text-shadow:4px 4px 5px #333}header i{font-size:0.7em}.wrapper{margin-top:4rem}.bg-gradient{background:linear-gradient(-45deg, #ad5389, #3c1053);background-size:350% 350%;-webkit-animation:gradient 10s ease infinite;animation:gradient 10s ease infinite;height:100vh}.bg-gradient.singingMode{background:linear-gradient(-45deg, #8360c3, #2ebf91);background-size:350% 350%}@-webkit-keyframes gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@keyframes gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}

button:focus{outline:none !important}.btngroups{margin-bottom:1rem}.main{width:100%}.btn-lg{margin:0 auto}

.record-btn{width:8rem;height:8rem;border-radius:50%;font-size:1.5rem;margin-top:5rem;text-align:center;line-height:8rem;cursor:pointer}.record-btn.pulse{-webkit-animation:pulse 1500ms infinite;animation:pulse 1500ms infinite}@-webkit-keyframes pulse{0%{transform:scale(0.95);box-shadow:0 0 0 10px #dc3546a8}40%{transform:scale(1);box-shadow:0 0 0 50px #dc354600}100%{transform:scale(0.95);box-shadow:0 0 0 10px #dc3546a8}}@keyframes pulse{0%{transform:scale(0.95);box-shadow:0 0 0 10px #dc3546a8}40%{transform:scale(1);box-shadow:0 0 0 50px #dc354600}100%{transform:scale(0.95);box-shadow:0 0 0 10px #dc3546a8}}

