header {
  font-family: "Titillium Web", sans-serif;
  font-weight: 300;
  h1 {
    font-size: 4rem;
    text-shadow: 4px 4px 5px rgba($color: #333333e0, $alpha: 1);
  }
  i {
    font-size: 0.7em;
  }
}

.wrapper {
  margin-top: 4rem;
}

.bg-gradient {
  background: linear-gradient(-45deg, #ad5389, #3c1053);
  background-size: 350% 350%;
  animation: gradient 10s ease infinite;
  height: 100vh;
  &.singingMode {
    background: linear-gradient(-45deg, #8360c3, #2ebf91);
    background-size: 350% 350%;
  }
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
